.hero-section { 
  position: relative; 
  height: 100vh; 
  min-height: 600px; /* Hauteur minimale pour les petits écrans */
  display: flex; 
  align-items: center; 
  overflow: hidden; 
} 

.hero-container { 
  display: flex; 
  align-items: center; 
  justify-content: space-between; 
  height: 100%; 
  position: relative; 
  z-index: 2; 
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
} 

.hero-content { 
  flex: 1; 
  max-width: 600px; 
} 

.hero-title { 
  font-size: 3.5rem; 
  margin-bottom: 1rem; 
  line-height: 1.2; 
} 

.hero-subtitle { 
  font-size: 1.8rem; 
  font-weight: 400; 
  margin-bottom: 1.5rem; 
  color: var(--dark); 
} 

.hero-text { 
  font-size: 1.1rem; 
  line-height: 1.6; 
  margin-bottom: 2rem; 
  color: var(--dark); 
  opacity: 0.9; 
} 

.hero-buttons { 
  display: flex; 
  gap: 1rem; 
  flex-wrap: wrap; /* Permet aux boutons de passer à la ligne si nécessaire */
} 

.btn-outline { 
  display: inline-block; 
  padding: 12px 28px; 
  background: transparent; 
  color: var(--primary-color); 
  border-radius: var(--rounded-sm); 
  font-weight: 600; 
  transition: all 0.3s ease; 
  border: 2px solid var(--primary-color); 
  cursor: pointer; 
} 

.btn-outline:hover { 
  background: var(--primary-color); 
  color: white; 
  transform: translateY(-3px); 
  box-shadow: var(--soft-shadow); 
} 

.hero-image { 
  flex: 1; 
  display: flex; 
  justify-content: center; 
} 

.hero-image img { 
  max-width: 100%; 
  height: auto; 
  max-height: 600px; 
  object-fit: contain; /* Assure que l'image conserve ses proportions */
} 

.hero-decoration .circle { 
  position: absolute; 
  border-radius: 50%; 
  z-index: 1; 
} 

.hero-decoration .pink { 
  width: 300px; 
  height: 300px; 
  background: rgba(255, 107, 157, 0.1); 
  top: -150px; 
  right: -150px; 
} 

.hero-decoration .light-pink { 
  width: 200px; 
  height: 200px; 
  background: rgba(255, 182, 193, 0.1); 
  bottom: 100px; 
  left: -100px; 
} 

.hero-decoration .accent { 
  width: 150px; 
  height: 150px; 
  background: rgba(212, 80, 121, 0.1); 
  bottom: -75px; 
  right: 20%; 
} 

/* Media queries pour la responsivité */
@media screen and (max-width: 1200px) {
  .hero-title {
    font-size: 3rem;
  }
  
  .hero-subtitle {
    font-size: 1.6rem;
  }
}

@media screen and (max-width: 1100px) {
  .hero-container {
    gap: 40px;
  }
}

@media screen and (max-width: 960px) { 
  .hero-container { 
    flex-direction: column; 
    text-align: center; 
    padding-top: 80px;
    justify-content: flex-start;
    gap: 30px;
  } 
  
  .hero-content { 
    margin-bottom: 2rem; 
    max-width: 100%;
  } 
  
  .hero-buttons { 
    justify-content: center; 
  } 
  
  .hero-title { 
    font-size: 2.5rem; 
  } 
  
  .hero-subtitle { 
    font-size: 1.5rem; 
  } 
  
  .hero-image {
    width: 80%;
    margin: 0 auto;
  }
  
  /* Ajustement des cercles décoratifs pour mobile */
  .hero-decoration .circle {
    display: none; /* On cache les cercles sur mobile pour plus de simplicité */
  }
} 

@media screen and (max-width: 600px) {
  .hero-section {
    min-height: 700px;
    height: auto;
    padding: 60px 0;
  }
  
  .hero-title {
    font-size: 2rem;
  }
  
  .hero-subtitle {
    font-size: 1.3rem;
  }
  
  .hero-text {
    font-size: 1rem;
  }
  
  .hero-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .hero-buttons a {
    width: 100%;
    max-width: 250px;
    text-align: center;
  }
  
  .hero-image {
    width: 100%;
  }
}

@media screen and (max-width: 400px) {
  .hero-title {
    font-size: 1.8rem;
  }
  
  .hero-container {
    padding: 60px 15px 0;
  }
}